.bg {
  background-image: url("/img/intro-bg-lg.svg");
  background-position-y: -210px;
  width: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
  @include r($lg) {
    background-position-y: -190px; }
  @include r(1500) {
    background-position-y: -140px; }
  @include r(1440) {
    background-position-y: -75px; }
  @include r($xs) {
    background-image: url("/img/intro-bg-sm.svg"); } }
