.info {
  margin-top: 70px;
  margin-bottom: 30px;
  @include r($lg) {
    margin-bottom: 0; }
  @include r($md) {
    margin-bottom: 50px; }
  @include r(1500) {
    margin-top: 0; }
  @include r($xs) {
    margin-top: 25px;
    padding-bottom: 50px; }
  .container {
    flex-direction: column;
    @include r($xs) {
      width: 100%;
      padding: 0 40px; }
    @include r(370) {
      padding: 0 25px; }
    .info__heading {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin: 0 auto 40px;
      max-width: 1500px;
      width: 100%;
      @include r($lg) {
        max-width: 1260px;
        margin-bottom: 25px; }
      @include r($md) {
        max-width: 1040px;
        margin-bottom: 15px; }
      @include r($xs) {
        width: 100%;
        justify-content: flex-start;
        margin-bottom: 40px; }
      h2 {
        margin-top: 0;
        margin-right: 70px;
        @include r($md) {
          margin-right: 60px; } }
      .btn-secondary {
        @include r($xs) {
          display: none; } } }
    .info__card {
      &:nth-of-type(4) {
        @include r($xs) {
          display: none; } } }
    .info__bottom {
      display: none;
      @include r($xs) {
        display: flex;
        justify-content: flex-start;
        align-items: center; } } } }
